<template>
  <div>
    <div class="form-group">
      <label for="qna_title">제목</label>
      <input
        type="text"
        class="form-control"
        id="qna_title"
        ref="qna_title"
        placeholder="제목을 입력하세요"
        v-model="qna_title"
      />
    </div>
    <div class="form-group">
      <label for="qna_userid">작성자</label>
      <input
        type="text"
        class="form-control"
        id="qna_userid"
        ref="qna_userid"
        placeholder="작성자를 입력하세요"
        v-model="qna_userid"
      />
    </div>
    <div class="form-group">
      <label for="qna_content">내용</label>
      <textarea
        class="form-control"
        id="qna_content"
        ref="qna_content"
        placeholder="작성자를 입력하세요"
        v-model="qna_content"
      />
    </div>
    <div class="text-right">
      <button class="btn btn-primary" @click="checkHandler">등록</button>
      <button class="btn btn-primary" @click="moveList">목록</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'Create',
  props: {
    type: { type: String },
  },
  data: function() {
    return {
      qna_no: '',
      qna_title: '',
      qna_content: '',
      qna_userid: '',
      qna_datetime: '',
      reply_content: '',
      reply_datetime: '',
      reply_userid: '',
    };
  },
  methods: {
    // 입력값 체크하기 - 체크가 성공하면 createHandler 호출
    checkHandler() {
      // 사용자 입력값 체크하기
      // 작성자, 제목, 내용
      // 없을 경우 각 항목에 맞는 메세지를 출력
      let err = true;
      let msg = '';
      !this.qna_title &&
        ((msg = '제목을 입력해주세요'), (err = false), this.$refs.qna_title.focus());
      err &&
        !this.qna_userid &&
        ((msg = '작성자를 입력해주세요'), (err = false), this.$refs.qna_userid.focus());
      err &&
        !this.qna_content &&
        ((msg = '내용을 입력해주세요'), (err = false), this.$refs.qna_content.focus());

      if (!err) alert(msg);
      // 만약, 내용이 다 입력되어 있다면 createHandler 호출
      else this.createHandler();
    },

    createHandler() {
      // 로컬스토리지에 저장된 데이터 가져오기
      axios
        .post('http://localhost:9999/happyhouse/api/qna', {
          qnaNo: this.qna_no,
          qnaTitle: this.qna_title,
          qnaContent: this.qna_content,
          qnaUserid: this.qna_userid,
          qnaDatetime: this.qna_datetime,
          replyContent: this.reply_content,
          replyDatetime: this.reply_datetime,
          replyUserid: this.reply_userid,
        })
        .then((response) => {
          if (response.data === 'SUCCESS') {
            alert('등록이 완료되었습니다.');
          } else {
            alert('등록이 실패하었습니다.');
          }
          this.moveList();
        })
        .catch((error) => {
          this.error = error;
        })
        .finally(() => {});
    },
    moveList() {
      this.$router.push('/');
    },
  },
};
</script>

<style scoped></style>
