<template>
  <div>
    <div>
      <div v-if="error">
        <pre>{{ error }}</pre>
      </div>
      <div>
        <table class="table table-bordered w-50">
          <tr>
            <th>글번호</th>
            <td>{{ item.qnaNo }}</td>
          </tr>
          <tr>
            <th>제목</th>
            <td>{{ item.qnaTitle }}</td>
          </tr>
          <tr>
            <th>작성자</th>
            <td>{{ item.qnaUserid }}</td>
          </tr>
          <tr>
            <th>등록일시</th>
            <td>{{ getFormatDate(item.qnaDatetime) }}</td>
          </tr>
          <tr>
            <th>내용</th>
            <td>{{ item.qnaContent }}</td>
          </tr>
        </table>

        <br />
        <div class="text-center">
          <router-link to="/" class="btn btn-primary">목록</router-link>

          <router-link :to="'/update?qnaNo=' + item.qnaNo" class="btn btn-primary"
            >수정</router-link
          >

          <router-link :to="'/delete?qnaNo=' + item.qnaNo" class="btn btn-primary"
            >삭제</router-link
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import moment from 'moment';

export default {
  name: 'Detail',
  data() {
    return {
      error: '',
    };
  },
  computed: {
    ...mapGetters(['item']),
  },
  created() {
    this.$store.dispatch('getBoard', `/qna/${this.$route.query.qnaNo}`);
  },
  methods: {
    getFormatDate(qnaDatetime) {
      return moment(new Date(qnaDatetime)).format('YYYY.MM.DD.HH:mm:ss');
    },
  },
};
</script>

<style></style>
