<template>
  <div>
    <div class="form-group">
      <input type="text" id="search" ref="search" placeholder="검색" v-model="search" />
      <button class="btn btn-primary">검색</button>
    </div>
    <div v-if="items.length">
      <table class="table table-bordered table-condensed">
        <colgroup>
          <col :style="{ width: '15%' }" />
          <col :style="{ width: '25%' }" />
          <col :style="{ width: '20%' }" />
          <col :style="{ width: '20%' }" />
          <col :style="{ width: '20%' }" />
        </colgroup>
        <tr>
          <th>글 번호</th>
          <th>제목</th>
          <th>작성자</th>
          <th>등록일시</th>
        </tr>
        <tr v-for="(board, index) in items" :key="index + '_items'">
          <td v-if="board.qnaTitle.includes(search)">{{ board.qnaNo }}</td>
          <td v-if="board.qnaTitle.includes(search)">
            <router-link :to="'/detail?qnaNo=' + board.qnaNo">{{ board.qnaTitle }}</router-link>
          </td>
          <td v-if="board.qnaTitle.includes(search)">{{ board.qnaUserid }}</td>
          <td v-if="board.qnaTitle.includes(search)">{{ getFormatDate(board.qnaDatetime) }}</td>
        </tr>
      </table>
    </div>
    <div v-else class="text-center">게시글이 없습니다.</div>
    <div class="text-right">
      <button class="btn btn-primary" @click="movePage">등록</button>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import moment from 'moment';

export default {
  name: 'List',
  data: function() {
    return {
      search: '',
    };
  },
  // <tr v-for="(emp, index) in items" :key="index + '_items'" v-if="emp.name.includes(search)">
  computed: {
    ...mapGetters(['items']),
  },
  created() {
    this.$store.dispatch('getBoards');
  },
  methods: {
    movePage() {
      this.$router.push('/create');
    },
    getFormatDate(qnaDatetime) {
      return moment(new Date(qnaDatetime)).format('YYYY.MM.DD');
    },
  },
};
</script>

<style></style>
