<template>
  <div>게시글 삭제중.....</div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'Delete',
  created() {
    const params = new URL(document.location).searchParams;

    axios
      .delete(`http://localhost:9999/happyhouse/api/qna/${params.get('qnaNo')}`)
      .then((response) => {
        if (response.data === 'SUCCESS') {
          alert('삭제가 완료되었습니다.');
        } else {
          alert('게시글 삭제에 실패하었습니다.');
        }
        this.moveList();
      })
      .catch((error) => {
        this.error = error;
      });
  },
  methods: {
    moveList() {
      this.$router.push('/');
    },
  },
};
</script>

